<template>
  <div>
    <mrc_ess_iconGroup :is_disabled='$store.state.iconstatus'
                       :showReturn='false'
                       @handleEdit='change'
                       @handleDel='remove'></mrc_ess_iconGroup>

    <group v-if="$route.params.groupName"
           :readonly='readonly'></group>
    <Levelcomponents v-else
                     :readonly='readonly'></Levelcomponents>
  </div>
</template>   

<script>
import Levelcomponents from './modules/Levelcomponents'
import group from './modules/group'
import { delete_postgrades_group, delete_postgrades } from '@/service/api/postgrades.js'
export default {
  components: {
    Levelcomponents, group
  },
  props: {},
  data () {
    return {
      switch1: false,
      readonly: null,
    };
  },
  created () { },
  mounted () {
    this.readonly = this.$store.state.iconstatus
  },
  methods: {
    change () {
      this.readonly = !this.readonly
      this.$store.commit('set_iconstatus', !this.$store.state.iconstatus)  //行内进图标显示
      // this.$route.params.iconstatus = !this.$route.params.iconstatus
    },
    remove () {  //删除
      this.$myConfirm({
        content: '确认要删除吗'
      }).then(res => {
        if (this.$store.state.subPage_name) {
          delete_postgrades_group({
            group_name: this.$store.state.subPage_name
          }).then(res => {
            if (res.code == 200) {
              this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
              this.$router.push({
                name: 'jobs_manage'
              })
            }
            else {
              this.$store.commit('newsTips', res.msg)
            }
          })
        }
        else {
          delete_postgrades({
            uuid: this.$store.state.uuid
          }).then(res => {
            if (res.code == 200) {
              this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
              this.$router.push({
                name: 'jobs_manage'
              })
            }
            else {
              this.$store.commit('newsTips', res.msg)
            }
          })
        }
      }).catch(err => { })
    },
  },
  computed: {},
  watch: {},
};
</script>

<style scoped>
.icon {
  padding: 15px 10px;
  color: white;
}
.icon-box {
  position: fixed;
  top: 75px;
  right: 30px;
}
.btn-icon {
  display: inline-block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: primary;
}
</style>
