<template>
  <v-form>
    <v-card>
      <v-card-title>
        <!-- <v-switch v-model="levelcomponentslist.is_used"
                    :label="levelcomponentslist.is_used?'启用':'不启用'"></v-switch> -->

        <mrc_ess_switch v-model="levelcomponentslist.is_used"
                        true-value='1'
                        false-value='0'
                        :readonly='readonly'
                        :true-text="$t('label_To_enable_the')"
                        :false-text="$t('label_disable')"></mrc_ess_switch>
      </v-card-title>
      <v-card-text>
        <v-row>
          <v-col cols="12">
            <!-- <v-text-field label="组名*"
                            append-icon="mdi-longitude"
                            :readonly='readonly'
                            :error-messages="$errors('levelcomponentslist','group_name',[...maxLength(30),...requiredRule])"
                            v-model="levelcomponentslist.group_name"
                            hide-details="auto"></v-text-field> -->
            <mrc_ess_multiText label="组名*"
                               :isNational="judegNational('group_name')"
                               :readonly='readonly'
                               :error-messages="$errors('levelcomponentslist','group_name',[...maxLength(30),...requiredRule])"
                               v-model="levelcomponentslist.group_name"></mrc_ess_multiText>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <mrc_ess_prompt label="单位*"
                            :disabled='readonly'
                            :multiple="true"
                            :errMsg="$errors(null,'units',requiredRule)"
                            v-model="units"
                            :obj="unit">
            </mrc_ess_prompt>
          </v-col>
        </v-row>

        <!-- 表格 -->
        <v-data-table :headers="headers"
                      :items="desserts"
                      sort-by="calories"
                      class="elevation-1"
                      hide-default-footer>
          <!-- 弹出框 -->
          <template v-slot:item.actions="{ item }">
            <v-icon small
                    class="mr-2"
                    @click="editItem(item)">
              mdi-pencil
            </v-icon>
            <v-icon small
                    :disabled="readonly"
                    @click="deleteItem(item)">
              mdi-delete
            </v-icon>
          </template>
          <template v-slot:no-data>
            <!-- <v-btn color="primary"
                     @click="initialize">Reset</v-btn> -->
          </template>
          <template v-slot:footer>
            <v-divider></v-divider>
            <span v-if="!readonly"
                  class="mt-1 pb-1 d-flex ml-5"
                  @click="editItem">
              <span style="color:#0F46C1;line-height: 28px;font-size:12px">新层级</span>
              <v-icon class="material-icons"
                      style="color:#0F46C1;font-size:22px">add</v-icon>
            </span>
          </template>
        </v-data-table>
        <!-- <div class="h1"
               v-if="!readonly">
            <span @click="editItem">新层级<span class="f1">+</span></span>
          </div> -->
      </v-card-text>
      <mrc_ess_submitBtn @success='success'
                         :loading='btnIsLoading'
                         @cancel='cancel'></mrc_ess_submitBtn>
    </v-card>
    <mrc_ess_myDialog v-model="dialog"
                      :title="$t('label_A_new_level')"
                      @success='save'
                      @cancel='close'
                      :disabled='!readonly'>
      <v-card-text>
        <v-row dense>
          <v-col cols="12">
            <!-- <v-text-field v-model="editedItem.job_grade_name" 
                          :readonly='readonly'
                          append-icon="mdi-longitude"
                          :error-messages="$errors('editedItem','job_grade_name',[...maxLength(30),...requiredRule])"
                          :label="$t('label_The_name_of_the_hierarchy')+'*'"></v-text-field> -->
            <mrc_ess_multiText v-model="editedItem.job_grade_name"
                               :isNational="judegNational('job_grade_name')"
                               :readonly='readonly'
                               :error-messages="$errors('editedItem','job_grade_name',[...maxLength(30),...requiredRule])"
                               :label="$t('label_The_name_of_the_hierarchy')+'*'"></mrc_ess_multiText>
          </v-col>
          <v-col cols="12">
            <!-- <v-text-field v-model="editedItem.job_grade_class"
                          :readonly='readonly'
                          :label="$t('label_The_hierarchy')+'*'"
                          :error-messages="$errors('editedItem','job_grade_class',between('1-50'))"></v-text-field> -->
            <!-- <mrc_ess_multiText v-model="editedItem.job_grade_class"
                               :readonly='readonly'
                               :isNational="judegNational('job_grade_class')"
                               :label="$t('label_The_hierarchy')+'*'"
                               :error-messages="$errors('editedItem','job_grade_class',between('1-50'))"></mrc_ess_multiText> -->
            <mrc_ess_NumInput v-model="editedItem.job_grade_class"
                              :readonly='readonly'
                              :label="$t('label_The_hierarchy')+'*'"
                              :error-messages="$errors('editedItem','job_grade_class',between('1-50'))"></mrc_ess_NumInput>
          </v-col>
          <v-col cols="12">
            <!-- <v-text-field v-model="editedItem.remark"
                          :readonly='readonly'
                          :label="$t('label_note')"></v-text-field> -->
            <mrc_ess_multiText v-model="editedItem.remark"
                               :isNational="judegNational('remark')"
                               :readonly='readonly'
                               :label="$t('label_note')"></mrc_ess_multiText>
          </v-col>
          <v-col cols="12">
            <!-- <v-text-field v-model="editedItem.sequence"
                          :readonly='readonly'
                          :error-messages="$errors('editedItem','sequence',between('1-50'))"
                          :label="$t('label_The_sorting')+'*'"></v-text-field> -->
            <mrc_ess_NumInput v-model="editedItem.sequence"
                              :readonly='!readonly'
                              :error-messages="$errors('editedItem','sequence',between('1-50'))"
                              :label="$t('label_The_sorting')+'*'"></mrc_ess_NumInput>

          </v-col>
        </v-row>
      </v-card-text>
    </mrc_ess_myDialog>
  </v-form>
</template> 

<style scoped>
.h1 {
  margin-top: 20px;
  margin-left: 20px;
  text-align: start;
  font-size: 22px;
  color: #0f46c1;
}
.f1 {
  font-size: 30px;
  color: #0f46c1;
}
.bt1 {
  display: inline-block;
  width: 25px;
  height: 25px;
  background-color: #ccc;
  line-height: 25px;
  text-align: center;
}
.sequenceinput {
  border-top: 1px black solid;
  border-bottom: 1px black solid;
  text-align: center;
}
</style> 
 
<script> 
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
import { get_postgradesdetail, get_postgrades_groupdetail, update_postgrades_group, add_postgrades, add_postgrades_group } from '@/service/api/postgrades.js'
export default {
  components: {},
  props: {
    readonly: {
      type: Boolean,
      default: false
    }
  },
  watch: {
  },
  data () {
    return {
      btnIsLoading: false,
      company_uuid: '',  //当前公司uuid
      switch1: 1,
      levelcomponentslist: {
        org_level_uuid: "", //单位名称（参照）
        group_name: "", //组名
        mrbase_sys_job_post_grades: [
          {
            sequence: '', //排序
            job_grade_class: '', //级别
            job_grade_shortcode: "", //短编码
            job_grade_name: "", //等级名
            remark: "" //备注
          }
        ],
        is_used: "1" //是否启用0 否 1 是
      }, //
      dialog: false,
      deletePostmanagement: [],//删除岗位等级列表
      desserts: [],
      editedIndex: -1,
      editedItem: {
        job_grade_name: '',
        job_grade_class: 0,
        remark: 0,
        sequence: 0,
      },
      defaultItem: {
        job_grade_name: '',
        job_grade_class: 0,
        remark: 0,
        sequence: 0,
      },
      unit: null,
      units: []
    };
  },
  validations: {
    levelcomponentslist: {
      group_name: {
        maxLength: maxLength(30),
        required
      }
    },
    editedItem: {
      job_grade_name: {
        maxLength: maxLength(30), required
      },
      job_grade_class: {
        between: between(1, 50)
      },
      remark: {
        maxLength: maxLength(500),
      },
      sequence: {
        between: between(1, 50)
      },
    },
    units: {
      required
    }
  },
  created () { },
  async mounted () {
    await this.national('mrbase_sys_job_post_grades')
    const user_uuid = JSON.parse(localStorage.getItem('userData')).uuid  //单位高级参照
    this.unit = { prompt_name: 'org_levels_manager_filter_company', user_uuid }
    if (this.$route.params.uuid) {
      this.$route.meta.title = this.$store.state.subPage_name
      this.initialize()
    }
    else {

    }
  },
  methods: {
    typedelmethods (oldarr, newarr, comparisonvalue) { //加typedel方法
      var result = []  //
      for (var i = 0; i < oldarr.length; i++) {
        var c_value = oldarr[i].comparisonvalue
        var isExist = false
        for (let j = 0; j < newarr.length; j++) {
          if (newarr[j].comparisonvalue == c_value) {
            isExist = true
            break
          }
        }
        if (!isExist) {
          result.push({ ...oldarr[i], '~type~': 'del' })
        }
      }
      return result
    },
    success () {  //提交
      this.$v.levelcomponentslist.$touch()
      this.$v.$touch()
      if (this.$v.levelcomponentslist.$error) return
      this.btnIsLoading = true
      this.desserts.map(item => {
        delete item.isTrusted
        return item
      })
      this.levelcomponentslist.org_level_uuid = this.units.join()
      // this.levelcomponentslist.is_used == true ? this.levelcomponentslist.is_used = 1 : this.levelcomponentslist.is_used = 0
      // console.log(this.levelcomponentslist.is_used);
      if (this.$route.params.uuid || this.$store.state.uuid) {
        //判断修改了那些岗位等级子表的内容
        // console.log([...this.desserts]); 
        // delete this.levelcomponentslist.mrbase_sys_job_post_grades
        // console.log(...this.deletePost);
        this.levelcomponentslist.mrbase_sys_job_post_grades = [...this.desserts, ...this.deletePost].map(item => {
          delete item.created_by
          delete item.updated_utc_datetime
          delete item.updated_by
          delete item.created_utc_datetime
          delete item.job_grade_id
          item["~table~"] = "mrbase_sys_job_post_grades"
          return item
        })
        // this.levelcomponentslist.post_grades_group_list = [...this.desserts, ...this.deletePost]
        // this.levelcomponentslist.org_level_uuid = this.companyName
        update_postgrades_group(this.levelcomponentslist).then(res => {
          if (res.code === '200') {
            this.$router.push({
              name: 'Post_level_management'
            })
          } else {
            this.$myConfirm({
              content: this.$t('message_The_modification_failed')
            })
            this.btnIsLoading = false
          }
        })
      }
      else {
        // console.log(this.desserts);
        this.levelcomponentslist.mrbase_sys_job_post_grades = this.desserts
        // this.levelcomponentslist.org_level_uuid = JSON.parse(localStorage.getItem('userData')).org_level_uuid
        // console.log(this.levelcomponentslist.mrbase_sys_job_post_grades);
        add_postgrades_group(this.levelcomponentslist).then(res => {
          if (res.code == 200) {
            this.$router.push({
              name: 'Post_level_management'
            })
          } else {
            this.$myConfirm({
              content: this.$t('message_Add_failed')
            })
            this.btnIsLoading = false
          }
        })
      }
    },
    cancel () {  //取消
      this.$router.push({
        name: 'Post_level_management'
      })
    },
    initialize () {
      get_postgrades_groupdetail({
        group_name: this.$route.params.groupName
      }).then(res => {
        this.levelcomponentslist = res.data
        this.units = res.data.org_level_uuid.length > 0 ? res.data.org_level_uuid.split(',') : []
        // this.levelcomponentslist.is_used == 1 ? this.levelcomponentslist.is_used = true : this.levelcomponentslist.is_used = false
        this.desserts = this.levelcomponentslist.mrbase_sys_job_post_grades
      })
    },
    editItem (item) {  //传了item就是修改  没传就是添加
      this.$v.editedItem.$reset()
      if (Object.keys(item)) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
      }
      this.dialog = true
    },

    deleteItem (item) {
      const index = this.desserts.indexOf(item)
      this.$myConfirm({
        content: '确认要删除该岗位等级吗'
      }).then(() => {
        const dd = this.desserts.splice(index, 1)
        if (dd[0].uuid || dd[0].client_uuid) this.deletePostmanagement.push(dd[0])
      })
      // confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {
      this.$v.editedItem.$touch()
      if (this.$v.editedItem.$error) return
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem)
      } else {
        this.desserts.push(this.editedItem)
      }
      this.close()
    },
  },
  computed: {
    headers () {
      return [
        { text: '层级名称', align: 'start', sortable: false, value: 'job_grade_name', },
        { text: '层级', sortable: false, value: 'job_grade_class' },
        { text: '备注', sortable: false, value: 'remark' },
        { text: ' 排序', value: 'sequence' },
        { text: 'Actions', value: 'actions', sortable: false },
      ]
    },
    deletePost () { //删除岗位等级列表
      if (this.deletePostmanagement.length > 0) {
        return this.deletePostmanagement.map(item => {
          return { ...item, '~type~': 'del' }
        })
      } else {
        return []
      }
    },
    companyName () {  //部门.currentCompany.uUID
      return this.company_uuid = JSON.parse(localStorage.getItem('userData')).currentCompany.uUID
    },
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
  }
};
</script>

<style scoped>
</style>
