<template>
  <v-form>
    <v-card>
      <v-card-title>
        <!-- <v-switch v-model="levelcomponentslist.is_used"
                    :label="levelcomponentslist.is_used?'启用':'不启用'"></v-switch> -->

        <mrc_ess_switch v-model="levelcomponentslist.is_used"
                        :true-value='1'
                        :false-value='0'
                        :readonly='readonly'
                        :true-text="$t('label_To_enable_the')"
                        :false-text="$t('label_disable')"></mrc_ess_switch>
      </v-card-title>
      <v-card-text>
        <v-row>
          <v-col cols="6">
            <!-- <v-text-field :label="$t('label_The_name_of_the_hierarchy')+'*'"
                            :rules='nodorules'
                            append-icon="mdi-longitude"
                            v-model="levelcomponentslist.job_grade_name"
                            :error-messages="$errors('levelcomponentslist','job_grade_name',requiredRule)"
                            hide-details="auto"
                            :readonly='readonly'></v-text-field> -->
            <mrc_ess_multiText :label="$t('label_The_name_of_the_hierarchy')+'*'"
                               :isNational="judegNational('job_grade_name')"
                               v-model="levelcomponentslist.job_grade_name"
                               :error-messages="$errors('levelcomponentslist','job_grade_name',requiredRule)"
                               :readonly='readonly'></mrc_ess_multiText>
          </v-col>
          <v-col cols="6">
            <mrc_ess_NumInput :label="$t('label_The_hierarchy')+'*'"
                              :error-messages="$errors('levelcomponentslist','job_grade_class',between('1-50'))"
                              v-model="levelcomponentslist.job_grade_class"
                              :readonly='!readonly'></mrc_ess_NumInput>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <!-- <v-text-field :label="$t('label_note')"
                            append-icon="mdi-longitude"
                            v-model="levelcomponentslist.remark"
                            :error-messages="$errors('levelcomponentslist','remark',maxLength(200))"
                            hide-details="auto"
                            :readonly='readonly'></v-text-field> -->
            <mrc_ess_multiText :label="$t('label_note')"
                               :isNational="judegNational('remark')"
                               v-model="levelcomponentslist.remark"
                               :error-messages="$errors('levelcomponentslist','remark',maxLength(200))"
                               :readonly='readonly'></mrc_ess_multiText>

          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <mrc_ess_prompt :label="$t('label_company')+'*'"
                            :disabled='readonly'
                            :multiple="true"
                            v-model="units"
                            :errMsg="$errors(null,'units',requiredRule)"
                            :obj="unit">
            </mrc_ess_prompt>
          </v-col>
        </v-row>
        <v-row v-if="readonly">
          <v-col cols="6">
            <mrc_ess_NumInput :label="$t('label_Number_of_posts_in_use')"
                              v-model="levelcomponentslist.staff_number"
                              :error-messages="$errors('levelcomponentslist','staff_number',between('1-100000'))"
                              :readonly='readonly'></mrc_ess_NumInput>
          </v-col>
          <v-col cols="6">
            <mrc_ess_NumInput :label="$t('label_The_sorting')+'*'"
                              :error-messages="$errors('levelcomponentslist','sequence',between('1-50'))"
                              v-model="levelcomponentslist.sequence"
                              :readonly='readonly'></mrc_ess_NumInput>
          </v-col>
        </v-row>
        <v-row v-else>
          <v-col cols="6">
            <mrc_ess_NumInput :label="$t('label_The_sorting')+'*'"
                              :error-messages="$errors('levelcomponentslist','sequence',between('1-50').concat(requiredRule))"
                              v-model="levelcomponentslist.sequence"
                              :readonly='readonly'></mrc_ess_NumInput>
          </v-col>
        </v-row>
      </v-card-text>
      <mrc_ess_submitBtn @success='success'
                         @cancel='cancel'
                         :loading='btnIsLoading'
                         v-if="!readonly"></mrc_ess_submitBtn>
    </v-card>
  </v-form>
</template>

<style scoped>
.bt1 {
  display: inline-block;
  width: 25px;
  height: 25px;
  background-color: #ccc;
  line-height: 25px;
  text-align: center;
}
.sequenceinput {
  border-top: 1px black solid;
  border-bottom: 1px black solid;
  text-align: center;
}
</style>
 
<script> 
import { update_postgrades, get_postgradesdetail, get_postgrades_groupdetail, update_postgrades_group, add_postgrades, add_postgrades_group } from '@/service/api/postgrades.js'
import { required, maxLength, between, email, url, numeric } from 'vuelidate/lib/validators'

export default {
  components: {},
  props: {
    readonly: {
      type: Boolean,
      default: false
    }
  },
  watch: {
  },
  data () {
    return {
      btnIsLoading: false,
      nodorules: [
        v => !!v || '不能为空',
        v => String(v).length <= 200 || '超过输入个数',
        v => !/[@#$%^&()<>?/]/.test(v) || '请勿输入非法字符',
        v => !/^ +| +$/g.test(v) || '不能输入空格',
      ],
      userrules: [
        v => String(v).length <= 20 || '超过输入个数',
        v => !/[^\w]/g.test(v) || '只能输入数字和字母'
      ],
      unit: null,
      units: [],
      switch1: false,
      levelcomponentslist: {
        job_grade_name: "",		//级别名称
        job_grade_shortcode: "",		//级别短编码
        job_grade_class: "",		//级别
        is_used: "1",		//是否启用 0 否 1 是
        sequence: "",		//排序
        remark: "",		//备注
        org_level_uuid: "",		//关联公司UUID
      }

    };
  },
  validations: {
    levelcomponentslist: {
      // group_name: {
      //   maxLength: maxLength(20),
      //   required
      // },
      job_grade_shortcode: {
        maxLength: maxLength(10),
      },
      job_grade_name: {
        maxLength: maxLength(20),
        required
      },
      job_grade_class: {
        maxLength: maxLength(10),
        between: between(1, 50)
      },
      remark: {
        maxLength: maxLength(200),
      },
      sequence: {
        maxLength: maxLength(10),
        between: between(1, 50),
        required
      },
      staff_number: {
        maxLength: maxLength(10),
        between: between(1, 100000)
      }
    },
    units: {
      required
    }
  },
  created () { },
  async mounted () {
    await this.national('mrbase_sys_job_post_grades')
    const user_uuid = this.$store.state.userData.uuid || JSON.parse(localStorage.getItem('userData')).uuid
    this.unit = { prompt_name: 'org_levels_manager_filter_company', user_uuid }
    if (this.$route.params.uuid || this.$store.state.uuid) {
      this.$route.meta.title = this.$store.state.subPage_name
      const res = await get_postgradesdetail({ uuid: this.$store.state.uuid })
      this.levelcomponentslist = res.data
      this.units = res.data.org_level_uuid.length > 0 ? res.data.org_level_uuid.split(',') : []
    }
    else {

    }
  },
  methods: {
    success () {  //提交
      this.$v.levelcomponentslist.$touch()
      this.$v.$touch()
      if (this.$v.levelcomponentslist.$error) return
      this.btnIsLoading = true
      this.levelcomponentslist.org_level_uuid = this.units.join()
      if (this.$route.params.uuid || this.$store.state.uuid) {
        delete this.levelcomponentslist.created_by
        delete this.levelcomponentslist.updated_utc_datetime
        delete this.levelcomponentslist.updated_by
        delete this.levelcomponentslist.created_utc_datetime
        // this.levelcomponentslist.is_used = 1
        update_postgrades(this.levelcomponentslist).then(res => {
          if (res.code == 200) {
            this.$router.push({
              name: 'Post_level_management'
            })
          }
          else if (res.code == 'mrbase_sys_job_post_grades.job_grade_class.required') {
            this.$myConfirm({
              content: this.$t('message_The_higher_the_number')
            })
            this.btnIsLoading = false
          }
          else {
            this.$myConfirm({
              content: this.$t('message_The_modification_failed')
            })
            this.btnIsLoading = false
          }
        })
      }
      else {
        add_postgrades(this.levelcomponentslist).then(res => {
          if (res.code == 200) {
            this.$router.push({
              name: 'Post_level_management'
            })
          }
          else if (res.code == 'mrbase_sys_job_post_grades.job_grade_class.required') {
            this.$myConfirm({
              content: this.$t('message_The_higher_the_number')
            })
            this.btnIsLoading = false
          }
          else if (res.code == 'mrbase_sys_job_post_grades.org_level_uuid.required') {
            this.$myConfirm({
              content: this.$t('message_Unit_required')
            })
            this.btnIsLoading = false
          }
          else {
            this.$myConfirm({
              content: this.$t('message_Add_failed')
            })
            this.btnIsLoading = false
          }
        })
      }
    },
    cancel () {  //取消
      this.$router.push({
        name: 'Post_level_management'
      })
    }
  },
  computed: {
    company_uuid () {  //当前公司
      return this.$store.state.currentCompany
    },
  }
};
</script>

<style scoped>
</style>
